  src: local("☺"), url(/react-task-cat/static/media/trebuc.bbec02d9.woff) format("woff"), url(/react-task-cat/static/media/trebuc.ea3fdfa6.ttf) format("truetype"), url(/react-task-cat/static/media/trebuc.536d58ec.svg) format("svg");
