body {
  font-family: "Trebuchet MS", sans-serif;
  font-weight: normal;
  color: #ffffff;
}

#root {
  height: 100%;
  width: 100%;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px 20px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5) 100%), url(/react-task-cat/static/media/Pattern.8e412cf5.png) 0 0 repeat;
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding: 20px 0;
  }
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}

.container h1 {
  margin-bottom: 25px;
  font-family: "Exo 2", sans-serif;
  font-weight: 100;
  font-size: 36px;
  text-align: center;
}

.row {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1120px;
}

.column-block {
  display: flex;
  flex-direction: column;
  margin: 30px;
  max-width: 320px;
  margin-bottom: 40px;
}

@media screen and (max-width: 768px) {
  .column-block:nth-child(1) {
    margin-right: 20px;
    margin-left: 20px;
  }
}

@media screen and (max-width: 425px) {
  .column-block:nth-child(1) {
    margin-right: 0;
    margin-left: 0;
  }
}

@media screen and (max-width: 992px) {
  .column-block:nth-child(2) {
    margin-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .column-block:nth-child(2) {
    margin-right: 20px;
    margin-left: 20px;
  }
}

@media screen and (max-width: 425px) {
  .column-block:nth-child(2) {
    margin-right: 0;
    margin-left: 0;
  }
}

.column-block:nth-child(3) {
  margin-right: 0;
}

@media screen and (max-width: 992px) {
  .column-block:nth-child(3) {
    margin-right: 40px;
    margin-left: 40px;
  }
}

@media screen and (max-width: 768px) {
  .column-block:nth-child(3) {
    margin-right: 20px;
    margin-left: 20px;
  }
}

@media screen and (max-width: 425px) {
  .column-block:nth-child(3) {
    margin-right: 0;
    margin-left: 0;
  }
}

.column {
  -webkit-clip-path: polygon(60px 0, 100% 0, 100% 50%, 100% 100%, 0 100%, 0 60px);
          clip-path: polygon(60px 0, 100% 0, 100% 50%, 100% 100%, 0 100%, 0 60px);
  position: relative;
  overflow: hidden;
  padding-top: 24px;
  padding-left: 49px;
  width: 320px;
  height: 480px;
  border: 4px solid #1698d9;
  background: #f2f2f2 url(/react-task-cat/static/media/cat.d63d2134.png) 0 bottom no-repeat;
  border-radius: 15px 15px 15px;
  margin-bottom: 15px;
}

.column:hover {
  border-color: #2ea8e6;
}

.column:hover::after {
  border-top: 60px solid #2ea8e6;
  border-right: 60px solid transparent;
}

.column:hover .column__weight {
  background-color: #2ea8e6;
}

.column:after {
  position: absolute;
  left: -1px;
  top: -1px;
  content: "";
  border-top: 60px solid #1698d9;
  border-right: 60px solid transparent;
}

.column__description {
  color: #666666;
  font-size: 16px;
  text-align: left;
  margin-bottom: 15px;
}

.column__title {
  color: #000000;
  font-size: 24px;
  font-weight: 700;
  text-align: left;
  margin-bottom: 15px;
}

.column__title p {
  font-size: 48px;
  font-weight: 700;
  text-align: left;
}

.column__promo {
  color: #666666;
  max-width: 127px;
  font-size: 14px;
  line-height: 16px;
  text-align: left;
}

.column__promo span {
  font-weight: 700;
}

.column__weight {
  position: absolute;
  content: "";
  bottom: 15px;
  right: 15px;
  color: #ffffff;
  font-size: 21px;
  text-align: center;
  width: 81px;
  height: 81px;
  background-color: #1698d9;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.column__weight p {
  font-size: 42px;
}

.column__note {
  text-shadow: 0 1px 0 #000000;
  color: #ffffff;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
}

.column__note a {
  color: #1698d9;
  font-weight: 700;
  border-bottom: 1px dashed #1698d9;
}

.column__note.disabled {
  color: #ffff66;
}

.column.active {
  border-color: #d91667;
}

.column.active::after {
  border-top: 60px solid #d91667;
  border-right: 60px solid transparent;
}

.column.active .column__weight {
  background-color: #d91667;
}

.column.activeHover:hover {
  border-color: #e52e7a;
}

.column.activeHover:hover::after {
  border-top: 60px solid #e52e7a;
  border-right: 60px solid transparent;
}

.column.activeHover:hover .column__weight {
  background-color: #e52e7a;
}

.column.disabled {
  background: linear-gradient(rgba(242, 242, 242, 0.5), rgba(242, 242, 242, 0.5)), url(/react-task-cat/static/media/cat.d63d2134.png) 0 bottom no-repeat;
  border-color: #b3b3b3;
  background-color: #f2f2f2;
}

.column.disabled::after {
  border-top: 60px solid #b3b3b3;
  border-right: 60px solid transparent;
}

.column.disabled .column__weight {
  background-color: #b3b3b3;
}

.column.disabled .column__description {
  color: #b3b3b3;
}

.column.disabled .column__title {
  color: #b3b3b3;
}

.column.disabled .column__promo {
  color: #b3b3b3;
}

